const digitalClock = document.querySelector('.digital-clock');
//clock:
const arrowSecond = document.querySelector('.clock-seconds__arrow'); //ÑÐµÐºÑƒÐ½Ð´Ð½Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ°
const arrowMinutes = document.querySelector('.clock-minutes__arrow'); //Ñ…Ð²Ð¸Ð»Ð¸Ð½Ð½Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ°
const arrowHours = document.querySelector('.clock-hours__arrow'); //Ð³Ð¾Ð´Ð¸Ð½Ð½Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ°

//Ð¼Ð°ÑÐ¸Ð²Ð¸ Ð´Ð»Ñ ÐºÐ¾Ñ€ÐµÐºÑ‚Ð½Ð¾Ð³Ð¾ Ð²Ð¸Ð²Ð¾Ð´Ñƒ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð½Ñ Date
const namesOfMonth = [
  'Ð¡Ñ–Ñ‡ÐµÐ½ÑŒ',
  'Ð›ÑŽÑ‚Ð¸Ð¹',
  'Ð‘ÐµÑ€ÐµÐ·ÐµÐ½ÑŒ',
  'ÐšÐ²Ñ–Ñ‚ÐµÐ½ÑŒ',
  'Ð¢Ñ€Ð°Ð²ÐµÐ½ÑŒ',
  'Ð§ÐµÑ€Ð²ÐµÐ½ÑŒ',
  'Ð›Ð¸Ð¿ÐµÐ½ÑŒ',
  'Ð¡ÐµÑ€Ð¿ÐµÐ½ÑŒ',
  'Ð’ÐµÑ€ÐµÑÐµÐ½ÑŒ',
  'Ð–Ð¾Ð²Ñ‚ÐµÐ½ÑŒ',
  'Ð›Ð¸ÑÑ‚Ð¾Ð¿Ð°Ð´',
  'Ð“Ñ€ÑƒÐ´ÐµÐ½ÑŒ',
];
const arrDay = [
  'ÐÐµÐ´Ñ–Ð»Ñ',
  'ÐŸÐ¾Ð½ÐµÐ´Ñ–Ð»Ð¾Ðº',
  'Ð’Ñ–Ð²Ñ‚Ð¾Ñ€Ð¾Ðº',
  'Ð¡ÐµÑ€ÐµÐ´Ð°',
  'Ð§ÐµÑ‚Ð²ÐµÑ€',
  'ÐŸ`ÑÑ‚Ð½Ð¸Ñ†Ñ',
  'Ð¡ÑƒÐ±Ð¾Ñ‚Ð°',
];

//Ð´Ð»Ñ Ð°ÐºÑ‚ÑƒÐ°Ð»ÑŒÐ½Ð¸Ñ… Ð´Ð°Ð½Ð¸Ñ…, Ð½Ð°Ð¿Ñ€Ð¸ÐºÐ»Ð°Ð´ Ð½Ð° Ð¼ÐµÐ¶Ñ– 23:59 Ñ– Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ð¾Ð³Ð¾ Ð´Ð½Ñ
setInterval(() => {
  //Ð”Ð°Ñ‚Ð°
  const currentTime = new Date();
  const currentDay = arrDay[currentTime.getDay()]; // Ð´ÐµÐ½ÑŒ Ñ‚Ð¸Ð¶Ð½Ñ
  const currentDate = currentTime.getDate(); // Ð´ÐµÐ½ÑŒ Ð¼Ñ–ÑÑÑ†Ñ
  const currentMonth = namesOfMonth[currentTime.getMonth()]; //Ð¼Ñ–ÑÑÑ†ÑŒ
  const currentYear = currentTime.getFullYear(); //Ñ€Ñ–Ðº

  //Ð³Ð¾Ð´Ð¸Ð½Ð½Ð¸Ðº
  const currentHour = currentTime.getHours(); //Ð³Ð¾Ð´Ð¸Ð½Ð¸
  const currentMinutes = currentTime.getMinutes(); //Ñ…Ð²Ð¸Ð»Ð¸Ð½Ð¸
  const currentSeconds = currentTime.getSeconds(); //ÑÐµÐºÑƒÐ½Ð´Ð¸

  //Ð¼ÐµÑ…Ð°Ð½Ñ–Ñ‡Ð½Ð¸Ð¹ Ð³Ð¾Ð´Ð¸Ð½Ð½Ð¸Ðº
  const changeSecond = (360 / 60) * currentSeconds; //360 Ð¿Ð¾Ð²Ð½Ðµ ÐºÐ¾Ð»Ð¾ / Ð½Ð° 60 ÑÐµÐº
  const changeMinutes = (360 / 60) * currentMinutes;
  const changeHours =
    (360 / 12) * currentHour + (360 / 12 / 60) * currentMinutes; //Ñ‰Ð¾Ð± ÑÑ‚Ñ€Ñ–Ð»ÐºÐ° Ð°Ð¿Ð¾ÐºÐ°Ð·ÑƒÐ²Ð°Ð»Ð° Ð½Ðµ Ñ‚Ñ€Ð¾Ð³Ð¾ Ð½Ð° Ð³Ð¾Ð´Ð¸Ð½Ñƒ, Ð° Ð¼Ñ–Ð¶ Ñ‚ÐµÐ¶

  //Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚ÑƒÑ”Ð¼Ð¾ Ð´Ð»Ñ ÐµÐ»ÐµÐºÑ‚Ñ€Ð¾Ð½Ð½Ð¾Ð³Ð¾ Ð³Ð¾Ð´Ð¸Ð½Ð½Ð¸ÐºÐ° (ÑÐºÑ‰Ð¾ 1 ÑÐµÐº, Ñ‰Ð¾Ð± Ð¿Ñ€Ð¾Ð¿Ð¸ÑÐ°Ð»Ð¾ 01)
  // padStart - Ð¿Ñ€Ð¸Ð¹Ð¼Ð°Ñ” (Ð´Ð¾Ð²Ð¶Ð¸Ð½Ð° ÑÐºÐ° Ð¼Ð°Ñ” Ð±ÑƒÑ‚Ð¸; ÑÐºÑ‰Ð¾ ÑÐ¸Ð¼Ð²Ð¾Ð»Ñ–Ð² Ð½Ðµ Ð²Ð¸ÑÑ‚Ð°Ñ‡Ð°Ñ”, Ñ‚Ð¾ Ñ‰Ð¾ Ð¿Ñ–Ð´ÑÑ‚Ð°Ð²Ð»ÑÑ”Ð¼Ð¾); Ð¿Ñ€Ð°Ñ†ÑŽÑ” Ñ–Ð· ÑÑ‚Ñ€Ð¾ÐºÐ¾ÑŽ
  const formatTime = `${currentHour
    .toString()
    .padStart(2, '0')} : ${currentMinutes
    .toString()
    .padStart(2, '0')} : ${currentSeconds.toString().padStart(2, '0')}`;

  //-----------------
  // console.log('Date:', currentTime);
  // console.log('Day:', currentTime.getDay());
  // console.log('Day from arr:', arrDay[currentTime.getDay()]);
  // console.log('Ñ‡Ð¸ÑÐ»Ð¾:', currentTime.getDate());
  // console.log(
  //   `getMonth(0-11): ${currentTime.getMonth()}, from arr: ${currentMonth}`
  // );
  // console.log('Year:', currentYear);
  //------------------
  day.textContent = currentDay;
  date.textContent = currentDate;
  month.textContent = currentMonth;
  year.textContent = currentYear;

  digitalClock.textContent = `ÐŸÐ¾Ñ‚Ð¾Ñ‡Ð½Ð¸Ð¹ Ñ‡Ð°Ñ: ${formatTime}`; //ÐµÐ»ÐµÐºÑ‚Ñ€Ð¾Ð½Ð½Ð¸Ð¹ Ð³Ð¾Ð´Ð¸Ð½Ð½Ð¸Ðº

  //ÑÑ‚Ñ€Ñ–Ð»ÐºÐ° Ð¿Ð¾ ÐºÐ¾Ð»Ñƒ
  arrowSecond.style.transform = `rotate(${changeSecond}deg)`; //ÑÐµÐºÑƒÐ½Ð´Ð½Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ° Ñ€ÑƒÑ…Ð°Ñ”Ñ‚ÑŒÑÑ Ð¿Ð¾ ÐºÐ¾Ð»Ñƒ
  arrowMinutes.style.transform = `rotate(${changeMinutes}deg)`; //Ñ…Ð²Ð¸Ð»Ð¸Ð½Ð½Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ° Ñ€ÑƒÑ…Ð°Ñ”Ñ‚ÑŒÑÑ Ð¿Ð¾ ÐºÐ¾Ð»Ñƒ
  arrowHours.style.transform = `rotate(${changeHours}deg)`; //Ð³Ð¾Ð´Ð¸Ð½Ð½Ð¸ÐºÐ¾Ð²Ð° ÑÑ‚Ñ€Ñ–Ð»ÐºÐ° Ñ€ÑƒÑ…Ð°Ñ”Ñ‚ÑŒÑÑ Ð¿Ð¾ ÐºÐ¾Ð»Ñƒ
}, 1000);

// ------------ 2. Ñ€ÐµÐºÐ»Ð°Ð¼Ð° ---------
const box = document.querySelector('.js-box');
const titleTimer = document.querySelector('.js-timer');

let counter = 4; //Ð´Ð»Ñ Ñ‚Ð°Ð¹Ð¼ÐµÑ€Ñƒ

setTimeout(() => {
  box.style.display = 'block'; //Ð¿Ð¾ÐºÐ°Ð·Ð°Ð»Ð¸ Ð¿ÐµÑ€ÑˆÐ¸Ð¹ Ñ€Ð°Ð· Ñ€ÐµÐºÐ»Ð°Ð¼Ñƒ

  //Ñ‚Ð°Ð¹Ð¼ÐµÑ€ Ð´Ð¾ Ð·Ð½Ð¸ÐºÐ½ÐµÐ½Ð½Ñ Ñ€ÐµÐºÐ»Ð°Ð¼Ð¸
  const id = setInterval(() => {
    counter -= 1;
    titleTimer.textContent = counter + 's';

    //ÑÐºÑ‰Ð¾ Ð´Ñ–Ð¹ÑˆÐ»Ð¸ Ð´Ð¾ Ð½ÑƒÐ»Ñ
    if (!counter) {
      clearInterval(id);
      // //Ð´Ð»Ñ Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ Ñ€ÐµÐºÐ»Ð°Ð¼Ð¸ ÐºÐ»Ñ–ÐºÐ¾Ð¼ Ð¿Ð¾ Ñ…Ñ€ÐµÑÑ‚Ð¸ÐºÑƒ
      // titleTimer.textContent = 'X';
      // titleTimer.addEventListener('click', onClick);
      box.style.display = 'none'; //Ð·Ð°ÐºÑ€Ð¸Ð»Ð¸ Ñ€ÐµÐºÐ»Ð°Ð¼Ñƒ
    }
  }, 1000);
}, 2000);

function onClick() {
  box.style.display = 'none';
}
//------------ 3 game "OneArmedBandit" (Promis)----------------------
const start = document.querySelector('.js-start');
const container = document.querySelector('.js-container');

start.addEventListener('click', onStart);

//map, Promise.allSettled(Ð¿Ñ€Ð¸Ð¹Ð¼Ð°Ñ” Ð¼Ð°ÑÐ¸Ð², Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” Ð¼Ð°ÑÐ¸Ð² Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð²)
function onStart() {
  let counter = 0;

  //console.dir(container); //Ð¼Ð¾Ð¶ÐµÐ¼Ð¾ Ð²Ð¸ÐºÐ¾Ñ€Ð¸ÑÑ‚Ð°Ñ‚Ð¸ children
  //Ñ‚Ð°Ðº ÑÐº Ñ†Ðµ Ð¿ÑÐµÐ²Ð´Ð¾Ð¼Ð°ÑÐ¸Ð², Ñ‚Ð¾ Ñ€Ð¾Ð·Ð¿Ð¸Ð»Ð¸Ð¼Ð¾ Ð¹Ð¾Ð³Ð¾(Ð¿ÐµÑ€ÐµÑ‚Ð²Ð¾Ñ€Ð¸Ð¼Ð¾ Ñƒ Ð¿Ð¾Ð²Ð½Ð¾Ñ†Ñ–Ð¹Ð½Ð¸Ð¹ Ð¼Ð°ÑÐ¸Ð² Ñ–Ð· Ð¼ÐµÑ‚Ð¾Ð´Ð°Ð¼Ð¸)
  [...container.children].forEach(box => (box.textContent = ''));

  const promises = [...container.children].map((_, i) => createPromise(i)); //Ð² 'map' ÑÐºÑ‰Ð¾ Ð¿ÐµÑ€ÑˆÐ¸Ð¹ Ð°Ð±Ð¾ Ð´Ñ€ÑƒÐ³Ð¸Ð¹ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€ Ð½Ðµ Ð¿Ð¾Ñ‚Ñ€Ñ–Ð±ÐµÐ½, Ñ‰Ð¾Ð± Ð¹Ð¾Ð³Ð¾ Ð½Ðµ Ð¿Ñ–Ð´ÑÐ²Ñ–Ñ‡ÑƒÐ²Ð°Ð»Ð¾ ÑÐº Ð½Ðµ Ð²Ð¸ÐºÐ¾Ñ€Ð¸ÑÑ‚Ð°Ð½Ð½Ð¸Ð¹, ÑÑ‚Ð°Ð²Ð¸Ð¼Ð¾ _

  // Promise.allSettled Ð¿Ñ€Ð¸Ð¹Ð¼Ð°Ñ” Ð¼Ð°ÑÐ¸Ð² Ð¿Ñ€Ð¾Ð¼Ñ–ÑÑ–Ð², Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” Ð¼Ð°ÑÐ¸Ð² Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð² (Ð² ÑÐºÐ¾Ð¼Ñƒ Ñ” ÑÑ‚Ð°Ñ‚ÑƒÑ Ñ– Ð·Ð½Ð°Ñ‡ÐµÐ½Ð½Ñ)
  Promise.allSettled(promises).then(items => {
    items.forEach((item, i) => {
      setTimeout(() => {
        if (item.status === 'fulfilled') {
          counter += 1;
        }
        //Ð¿Ð¾ Ñ‡ÐµÑ€Ð·Ñ– Ð² ÐºÐ¾Ð¶Ð½Ðµ 'Ð²Ñ–ÐºÐ¾Ð½Ñ†Ðµ' Ð¿Ñ–Ð´ÑÐ°Ð²Ð»ÑÑ”Ð¼Ð¾ Ð² Ñ‚ÐµÐºÑÑ‚Ð¾Ð²Ð¸Ð¹ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ item.value, ÑÐºÑ‰Ð¾ Ð½Ðµ Ð¼Ð°Ñ” Ñ‚Ð¾ item.reason
        container.children[i].textContent = item.value || item.reason;

        //Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÑÑ”Ð¼Ð¾ Ñ‡Ð¸ Ð¿ÐµÑ€ÐµÐ¼Ñ–Ð³ Ñ‡Ð¸ Ð¿Ñ€Ð¾Ð³Ñ€Ð°Ñˆ
        if (container.children.length - 1 === i) {
          setTimeout(() => {
            if (counter === container.children.length || !counter) {
              alert('Winner');
            } else {
              alert('Lost money');
            }
          }, 500);
        }
      }, i * 1000);
    });
  });
}

function createPromise() {
  return new Promise((res, rej) => {
    const random = Math.random();

    if (random > 0.5) {
      res('ðŸ¤‘');
    } else {
      rej('ðŸ˜ˆ');
    }
  });
}
